function variantsSlider() {
    var swiper = new Swiper('.variants .swiper-container', {
        slidesPerView: 3,
        spaceBetween: 29,
        observer: true,
        observeParents: true,
        observeSlideChildren: true,
        navigation: {
            nextEl: '.variants .swiper-button-next',
            prevEl: '.variants .swiper-button-prev',
        },
        pagination: {
            el: '.variants .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
        breakpoints: {
            320: {
                spaceBetween: 10,
                slidesPerView: "auto",
            },
            1200: {
                spaceBetween: 29,
                slidesPerView: 3,
            },
        }
    })
}

function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        spaceBetween: 10,
        slidesPerView: "auto",
        freeMode: true,
        watchSlidesProgress: true,
        autoplay: false,
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 20,
        slidesPerView: "auto",
        autoplay: false,
        thumbs: {
            swiper: swiper,
        },
        navigation: {
            nextEl: '.gallery .swiper-button-next',
            prevEl: '.gallery .swiper-button-prev',
        },
    });
}

function clientsSlider() {
    var swiper = new Swiper('.clients .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        navigation: {
            nextEl: '.clients .swiper-button-next',
            prevEl: '.clients .swiper-button-prev',
        },
        pagination: {
            el: '.clients .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
        breakpoints: {
            320: {
                loop: false,
            },
            600: {
                loop: true,
            },
        }
    })
}

variantsSlider()
gallery()
clientsSlider()
