$(".tab").click(function() {
    let parentBlock = $(this).parents("._tabs-parent")
    let tabId = $(this).attr("data-tab")
    console.log(parentBlock)
    $(parentBlock).find(".tab").removeClass("_active")
    $(this).addClass("_active")
    $(parentBlock).find(".tab-content").hide(300)
    $(parentBlock).find(`.${tabId}`).show(300)
})

function removeActiveClasses(blocks) {
    blocks.forEach(block => {
        block.classList.remove("_active")
    })
}


let calcMinus = document.querySelectorAll(".cartcalc .ccalc-minus")
let calcPlus = document.querySelectorAll(".cartcalc .ccalc-plus")


function cartCalc() {

    calcMinus.forEach(el => {
        el.onclick = function() {
            let input = el.closest('.cartcalc').childNodes[3]
            let a = input.value;
            console.log(input.value);
            if (a > 1) {
                let b = +a - 1;
                input.value = b;
            } else {
                input.value = a;
            }
        }
    });

    calcPlus.forEach(el => {
        el.onclick = function() {
            let input = el.closest('.cartcalc').childNodes[3]
            let a = input.value;
            let b = +a + 1;
            input.value = b;
        }
    });
}

cartCalc()
